@import url('https://fonts.googleapis.com/css?family=Raleway:400,500');

* {
  margin: 0;
  padding: 0;
  list-style: none;
  vertical-align: baseline;
}

div {
  position: relative;
  z-index: 2;
}

body {
  background-color: #333;
  color: #333;
  font-family: 'Raleway', sans-serif;
  font-weight: 400;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.footer {
  max-width: 600px;
  text-align: center;
  display: block;
  margin: 15px auto 30px auto;
  font-size: 0.7em;
  color: #fff;

  .footer-link {
    font-weight: 500;
    text-decoration: none;
    color: #fff;
  }
}

.quote-box {
  border-radius: 25px;
  position: relative;
  margin:8% auto auto auto;
  max-width: 600px;
  padding: 40px 50px;
  display: table;
  background-color: #fff;

  .quote-text {
    text-align: center;
    max-width: 600px;
    height: auto;
    clear: both;
    font-weight: 500;
    font-size: 1.25em;
  }

  .quote-author {
    max-width: 450px;
    height: auto;
    clear: both;
    padding-top: 20px;
    font-size: 0.7em;
    text-align: right;
  }

  .buttons {
    max-width: 450px;
    margin: auto;
    display: block;

    .icon {
      font-size: 0.7em;
      margin-bottom: 0.5em;
    }

    .button {
      height: 38px;
      border: none;
      border-radius: 3px;
      color: #fff;
      background-color: #333;
      outline: none;
      font-size: 0.7em;
      padding: 8px 18px 6px 18px;
      margin-top: 30px;
      opacity: 1;
      cursor: pointer;

      &:hover {
        opacity: 0.9;
      }

      &.tweet-quote,
      &.tumblr-quote {
        float: left;
        padding: 0px;
        padding-top: 8px;
        text-align: center;
        font-size: 1.2em;
        margin-right: 5px;
        max-height: 60px;
        width: 40px;
      }

      &.new-quote {
        float: right;
      }
    }
  }
}